/* retire-lah-client/src/index.css */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-brand-bg-dark text-brand-text-light font-sans antialiased tracking-tight selection:bg-brand-primary-action selection:text-white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  h1, h2, h3, h4, h5, h6 { @apply font-semibold text-brand-text-light; }
  h1 { @apply text-3xl sm:text-4xl md:text-5xl; }
  h2 { @apply text-xl sm:text-2xl md:text-3xl; } 
  h3 { @apply text-lg sm:text-xl md:text-2xl; } 
  p { @apply text-brand-text-medium leading-relaxed; }
}

@layer components {
  /* Form Element Base Styles for Dark Mode */
  .form-input,
  .form-select {
    @apply block w-full rounded-lg border-brand-border-dark bg-brand-card-bg-dark text-brand-text-light 
           shadow-subtle-dark 
           focus:border-brand-primary-action focus:ring-2 focus:ring-brand-primary-action focus:ring-opacity-60
           placeholder-brand-text-dim placeholder-opacity-70 
           text-sm sm:text-base py-2.5 px-3.5;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  .form-select { 
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%239CA3AF' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e"); /* Gray-400 arrow for dark mode */
    background-position: right 0.75rem center; background-repeat: no-repeat; background-size: 1.25em 1.25em; padding-right: 2.75rem;
  }
  label { 
    @apply block text-xs sm:text-sm font-semibold text-brand-text-medium mb-1.5;
  }

  /* Button Base Styles for Dark Mode */
  .btn { @apply font-semibold py-2.5 px-5 rounded-lg shadow-subtle-dark tracking-wide text-sm sm:text-base transition-all duration-150 ease-in-out transform focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-brand-bg-dark disabled:opacity-50 disabled:cursor-not-allowed disabled:shadow-none disabled:hover:brightness-100; }
  .btn-primary { @apply btn bg-brand-primary-action text-white hover:bg-brand-primary-action-hover active:scale-[0.98] focus:ring-brand-primary-action; }
  .btn-secondary { @apply btn bg-brand-secondary-action text-white hover:bg-brand-secondary-action-hover active:scale-[0.98] focus:ring-brand-secondary-action; }
  .btn-accent { @apply btn bg-brand-accent-highlight text-brand-bg-dark hover:bg-brand-accent-highlight-hover active:scale-[0.98] focus:ring-brand-accent-highlight; }
  .btn-outline { @apply btn bg-transparent border-2 active:bg-opacity-20; } /* General outline */
  /* Specific outline buttons for dark mode, ensuring text and border colors are visible */
  .btn-outline-primary-dark { @apply btn-outline text-brand-primary-action border-brand-primary-action hover:bg-brand-primary-action hover:text-white; }
  .btn-outline-secondary-dark { @apply btn-outline text-brand-secondary-action border-brand-secondary-action hover:bg-brand-secondary-action hover:text-white; }


  /* Tooltip Styles - Group Hover Version for Dark Mode */
  .tooltip-trigger { 
    /* The 'group' class MUST be applied in JSX/HTML, NOT here with @apply */
    @apply relative inline-block cursor-help; 
  }
  .tooltip-content { 
    @apply absolute z-30 w-max max-w-xs px-3 py-2 text-xs font-medium text-brand-bg-dark bg-brand-text-light rounded-lg shadow-xl 
           opacity-0 invisible group-hover:opacity-100 group-hover:visible 
           transition-opacity duration-200 ease-in-out;
    bottom: 135%; 
    left: 50%;
    transform: translateX(-50%);
    pointer-events: none; 
  }
  .tooltip-content::after {
    content: ""; 
    position: absolute; 
    top: 100%; 
    left: 50%; 
    margin-left: -5px;
    border-width: 6px; 
    border-style: solid; 
    border-color: #E5E7EB transparent transparent transparent; /* brand-text-light (Gray-200) for arrow */
  }
}

/* Scrollbar styles */
::-webkit-scrollbar { width: 8px; height: 8px; }
::-webkit-scrollbar-track { background: #1F2937; border-radius: 10px; } /* Darker track (Gray-800) */
::-webkit-scrollbar-thumb { background: #4B5563; border-radius: 10px; } /* Medium dark thumb (Gray-600) */
::-webkit-scrollbar-thumb:hover { background: #6B7280; } /* Lighter thumb on hover (Gray-500) */